<template>
	<div class="body">
		<!--投资模块-->
		<div class="cash_block" v-if="cash_type == 1">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">投资</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table ref="multipleTable" :data="tableData1" border style="width: 100%">
						<el-table-column prop="fund_name" label="投资基金名称">
						</el-table-column>
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="fund_date" label="出资日期">
						</el-table-column>
						<el-table-column prop="fee" label="金额（万元）">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column prop="note" label="备注">
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--回款返本模块-->
		<div class="cash_block" v-if="cash_type == 2">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">回款返本</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table ref="multipleTable" :data="tableData2" border style="width: 100%">
						<el-table-column prop="fund_name" label="回款基金名称">
						</el-table-column>
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="date" label="日期">
						</el-table-column>
						<el-table-column prop="fee" label="金额（万元）">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column prop="note" label="备注">
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--回款收益模块-->
		<div class="cash_block" v-if="cash_type == 3">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">回款收益</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table :data="tableData3" border style="width: 100%">
						<el-table-column prop="fund_name" label="回款基金名称">
						</el-table-column>
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="date" label="出资日期">
						</el-table-column>
						<el-table-column prop="fee" label="金额（万元）">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column prop="note" label="备注">
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--支出模块-->
		<div class="cash_block" v-if="cash_type == 4">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">支出</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table :data="tableData4" border style="width: 100%">
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="plan_service_fee" label="应缴认购服务费">
						</el-table-column>
						<el-table-column prop="service_fee" label="实缴认购服务费">
						</el-table-column>
						<el-table-column prop="receive_date" label="到账日期">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getBackInvestorCashListApi,getBackIncomeInvestorCashListApi,getInvestInvestorCashListApi,getPayInvestorCashListApi } from '../../config/api'
	
	export default {
		name: 'investerManageCashDetail',
		data() {
			return {
				id: '',
				count: '',
				type: '',//列表中的inside_type字段的值，标示是否为母基金还是自管基金
				cash_type: '',//1代表投资；2代表回款返本；3代表回款收益；4代表支出
				company_id: '',
				tableData1: [],//投资
				tableData2: [],//回款返本
				tableData3: [],//回款收益
				tableData4: [],//支出
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.company_id = this.$route.query.company_id;
			this.count = this.$route.query.count;
			this.type = this.$route.query.type;
			this.cash_type = this.$route.query.cash_type;
			if (this.cash_type == 1) {
				this.getInitDataInvest()
			} else if(this.cash_type == 2){
				this.getInitDataBack()
			} else if(this.cash_type == 3){
				this.getInitDataBackIcome()
			} else {
				this.getInitDataPay()
			}
		},
		mounted() {},
		components: {},
		methods: {
			getInitDataInvest(){
				getInvestInvestorCashListApi({
					investor_id: this.id,
					count: this.count,
					type: this.type
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData1.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getInitDataBack(){
				getBackInvestorCashListApi({
					investor_id: this.id,
					count: this.count,
					type: this.type
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData2.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getInitDataBackIcome(){
				getBackIncomeInvestorCashListApi({
					investor_id: this.id,
					count: this.count,
					type: this.type
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData3.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getInitDataPay(){
				getPayInvestorCashListApi({
					investor_id: this.id,
					count: this.count,
					sg_company_manager_id: this.company_id
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData4.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 20px 10px 30px 10px;
	}
	.cash_block_title{
		margin: 22px 0;
	}
	.cash_block_title .name{
		color: #6292F4;
		font-size: 16px;
	}
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 12px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


